<template>
  <view>
   <view v-if="show1">
     <input type="text" v-model="title"  placeholder="上传一点视频" maxlength="10">
     <uni-file-picker 
     	v-model="imageValue" 
     	fileMediatype="video" 
      limit="1"
     	mode="grid" 
     	@select="select" 
     	@progress="progress" 
     	@success="success" 
     	@fail="fail" 
     />
     <button type="default" @click="add_videos" :disabled="dis_btn" >点击发布</button>
     <view v-for="(list,i) in video_url" :key="i" class="images_box">
       <view class="remove_class">
         <uni-icons type="closeempty" size="40" class="show_remove" @click="remove1(index,i)"></uni-icons>
       </view>
       <text class="images_title">{{list.title}}</text>
      <video :src="list.url" class="image_style" controls></video>
     </view>
   </view>
   <view v-else-if="show2">
     <input type="text" v-model="title"  placeholder="要上传一张图片这里写他的标题" maxlength="10">
     <uni-file-picker 
     	v-model="imageValue" 
     	fileMediatype="image" 
      limit="1"
     	mode="grid" 
     	@select="select" 
     	@progress="progress" 
     	@success="success" 
     	@fail="fail" 
     />
     <button type="default" @click="add_images" :disabled="dis_btn" >点击发布</button>
     <view v-for="(list,i) in img_url" :key="i" class="images_box">
       <view class="remove_class">
         <uni-icons type="closeempty" size="40" class="show_remove" @click="remove2(index,i)"></uni-icons>
       </view>
       <text class="images_title">{{list.title}}</text>
      <image :src="list.url" mode="aspectFit" class="image_style"></image>
     </view>
   </view>
   <view v-else-if="show3">
     <input type="text" v-model="title"  placeholder="要上传一张图片这里写他的标题" maxlength="10">
     <uni-file-picker 
     	v-model="imageValue" 
     	fileMediatype="image" 
      limit="1"
     	mode="grid" 
     	@select="select" 
     	@progress="progress" 
     	@success="success" 
     	@fail="fail" 
     />
     <button type="default" @click="add_images" :disabled="dis_btn" >点击发布</button>
     <view v-for="(list,i) in img_url" :key="i" class="images_box">
       <text class="images_title">{{list.title}}</text>
      <image :src="list.url" mode="aspectFit" class="image_style"></image>
     </view>
   </view>
   <view v-else-if="show4">
     <input type="text" v-model="title"  placeholder="要上传一张图片这里写他的标题" maxlength="10">
     <uni-file-picker 
     	v-model="imageValue" 
     	fileMediatype="image" 
      limit="1"
     	mode="grid" 
     	@select="select" 
     	@progress="progress" 
     	@success="success" 
     	@fail="fail" 
     />
     <button type="default" @click="add_images" :disabled="dis_btn" >点击发布</button>
     <view v-for="(list,i) in img_url" :key="i" class="images_box">
       <text class="images_title">{{list.title}}</text>
      <image :src="list.url" mode="aspectFit" class="image_style"></image>
     </view>
   </view>
   <view v-else>
     第5个内容
   </view>
  </view>
</template>

<script>
  let id ;
  export default {
  data() {
    return {
      title:"",
      dis_btn:true,
      imageValue:[],
      show1:false,
      show2:false,
      show3:false,
      show4:false,
      show5:false ,
      index:0,
      gamew:[],
      data_image:{},
      img_url:[],
      video_url:[],
      data_video:{}
    }
  },
  onLoad(options) {
    //页面加载时的内容
          switch(options.id) {
            case '1':
              this.show1 = true;
              break;
            case '2':
              this.show2 = true;
              break;
            case '3':
              this.show3 = true;
              break;
            case '4':
              this.show4 = true;
              break;
            case '5':
              this.show5 = true;
              break;
            default:
          }
  
   this.get_images()
   this.get_video()
    },
    onReachBottom() {
      this.get_data()
      this.get_videos()
    },
    methods: {
      remove1(index,i){
        id = this.video_url[i]._id
        uni.showModal({
          content:"确定删除吗",
          success: (res) => {
            if(res.confirm)
            {
              this.video_delete()
            }
          }
        })
      },
      remove2(index,i){
        id = this.img_url[i]._id
        uni.showModal({
          content:"确定删除吗",
          success: (res) => {
            if(res.confirm)
            {
              this.img_delete()
            }
          }
        })
      },
      img_delete(){
        uniCloud.callFunction({
          //调用删除数据库云函数
          name:"remove_list",
          data:{id,table:"list_images"}
        }).then(res=>{
         uni.showToast({
           //图标删除
           title:"删除成功"
         })
         //删除完毕跳转
         setTimeout(()=>{
           uni.reLaunch({
            url: '../../subpkg/index/index?id=2'
           },500)
         })
        })
      },
      video_delete(){
        uniCloud.callFunction({
          //调用删除数据库云函数
          name:"remove_list",
          data:{id,table:"list_video"}
        }).then(res=>{
         uni.showToast({
           //图标删除
           title:"删除成功"
         })
         //删除完毕跳转
         setTimeout(()=>{
           uni.reLaunch({
            url: '../../subpkg/index/index?id=1'
           },500)
         })
        })
      },
      get_video(){
        uniCloud.callFunction({
          name:"get_list",
          data:{
            collectionName:"list_video"
          }
        }).then(res=>{
          this.video_url = res.result.data
        })
      },
      get_videos(){
        uniCloud.callFunction({
          name:"get_list",
          data:{
            //传递要读取的数据表
            collectionName:"list_video",
            //刷新几个数据 一开始刷新几条数据
            skip:this.video_url.length
          }
        }).then(res=>{
          //把新数据和久数据拼接起来 触底就刷新五条记录
          let oldlist = this.video_url
          this.video_url = [...oldlist,...res.result.data ]
        })
      },
      add_videos(){
        let titles = this.title
        let detail = this.data_video
        if(titles==="")
        {
          return "没输入东西"
        }
        else if(detail==="")
        {
          return "没输入"
        }
        uniCloud.callFunction({
          name:"add_data_images",
          data:{
            table:"list_video",
            detail,
            titles
            }
        }).then(res=>{
           uni.reLaunch({
                  url: '../../subpkg/index/index?id=1'
                });
        })
      },
      //照片加载刷新
      get_data(){
        uniCloud.callFunction({
          name:"get_list",
          data:{
            //传递要读取的数据表
            collectionName:"list_images",
            //刷新几个数据 一开始刷新几条数据
            skip:this.img_url.length
          }
        }).then(res=>{
          //把新数据和久数据拼接起来 触底就刷新五条记录
          let oldlist = this.img_url
          let newlist = [...oldlist,...res.result.data ]
          this.img_url = newlist
        })
      },
      //获取照片
      get_images(){
        uniCloud.callFunction({
          name:"get_list",
          data:{
            collectionName:"list_images"
          }
        }).then(res=>{
          this.img_url = res.result.data
        })
      },
      ///添加图片和文字
      add_images(){
        let titles = this.title
        let detail = this.data_image
        if(titles==="")
        {
          return "没输入东西"
        }
        else if(detail==="")
        {
          return "没输入"
        }
        uniCloud.callFunction({
          name:"add_data_images",
          data:{
            detail,
            table:"list_images",
            titles}
        }).then(res=>{
           uni.reLaunch({
                  url: '../../subpkg/index/index?id=2'
                });
        })
      },
      			select(e){
      				console.log('选择文件：',e)
      			},
      			// 获取上传进度
      			progress(e){
      				console.log('上传进度：',e)
      			},
      			// 上传成功
      			success(e){
      				console.log('上传成功',e.tempFilePaths[0])
              this.data_video = e.tempFilePaths
              this.data_image = e.tempFilePaths
              this.dis_btn = false
      			},
      			// 上传失败
      			fail(e){
      				console.log('上传失败：',e)
      			}
    }
  }
</script>

<style>
  .remove_class{
    background-color: #ffaaff;
    width: 100px;
    height: 45px;
    margin-left: 75%;
  }
video{
	width: 750rpx;
}
input[type="text"] {
  padding: 10px;
  border: 2px solid #ffaaff;
  border-radius: 5px;
  color: blueviolet;
  font-size: 16px;
  outline: none;
}
.images_box{
  margin-top: 20px;
  text-align: center;
  border: 2px solid red;
  height: 45vh;
}
  .images_title {
    font-size: 18px;
    color: aquamarine;
    font-weight: bold;
    margin-bottom: 10px;
  }
  
  .image_style {
    width: 95%;
    margin-left: 2.5%;
    border-radius: 10px;
  }
</style>
